                    project)</v-list-item-content
                  >
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </div>
          <v-form ref="form" v-model="form">
            <v-row>
              <v-col
                cols="12"
                v-if="data.questions && data.questions.length > 0"
                sm="8"
              >
                <template v-for="(q, index) in answer">
                  <v-textarea
                    :label="`${q.question}`"
