                      >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="cost"
                      :rules="[rules.required]"
                      label="Total Team Lead Wage"
                    >
                      <v-icon slot="prepend" color="red"
